(function() {
  angular.module('workloadPlanner').directive('acmeMalarkey', function() {
    var MalarkeyController, directive, linkFunc;
    MalarkeyController = function($log, githubContributor) {
      'ngInject';
      var activate, getContributors, vm;
      vm = this;
      activate = function() {
        return getContributors().then(function() {});
      };
      getContributors = function() {
        return githubContributor.getContributors(10).then(function(data) {
          vm.contributors = data;
          return vm.contributors;
        });
      };
      vm.contributors = [];
      activate();
    };
    linkFunc = function(scope, el, attr, vm) {
      var typist, watcher;
      watcher = void 0;
      typist = malarkey(el[0], {
        typeSpeed: 40,
        deleteSpeed: 40,
        pauseDelay: 800,
        loop: true,
        postfix: ' '
      });
      el.addClass('acme-malarkey');
      angular.forEach(scope.extraValues, function(value) {
        typist.type(value).pause()["delete"]();
      });
      watcher = scope.$watch('vm.contributors', function() {
        angular.forEach(vm.contributors, function(contributor) {
          typist.type(contributor.login).pause()["delete"]();
        });
      });
      scope.$on('$destroy', function() {
        watcher();
      });
    };
    return directive = {
      restrict: 'E',
      scope: {
        extraValues: '='
      },
      template: '&nbsp;',
      link: linkFunc,
      controller: MalarkeyController,
      controllerAs: 'vm'
    };
  });

}).call(this);



(function() {
  angular.module('workloadPlanner').factory('githubContributor', function($log, $http) {
    'ngInject';
    var apiHost, getContributors, service;
    apiHost = 'https://api.github.com/repos/Swiip/generator-gulp-angular';
    getContributors = function(limit) {
      var getContributorsComplete, getContributorsFailed;
      if (limit == null) {
        limit = 30;
      }
      getContributorsComplete = function(response) {
        return response.data;
      };
      getContributorsFailed = function(error) {
        $log.error('XHR Failed for getContributors.\n' + angular.toJson(error.data, true));
      };
      return $http.get(apiHost + '/contributors?per_page=' + limit).then(getContributorsComplete)["catch"](getContributorsFailed);
    };
    return service = {
      apiHost: apiHost,
      getContributors: getContributors
    };
  });

}).call(this);



(function() {
  angular.module('workloadPlanner').service('workloadData', function(localStorage, $log, $timeout) {
    'ngInject';
    var _generatePriorityMatrix, diff, getNextId, getPersons, getPriorityMatrix, getTasks, service, setPersons, setPriorityMatrix, setTasks;
    diff = function(a, b) {
      if (a.length > b.length) {
        return a.filter(function(i) {
          return b.indexOf(i) < 0;
        });
      } else {
        return b.filter(function(i) {
          return a.indexOf(i) < 0;
        });
      }
    };
    getPersons = function() {
      return JSON.parse(localStorage.getItem('workloadData-persons') || '[]');
    };
    setPersons = function(persons, person) {
      var j, k, len, len1, matrix, old, p, ref, sum, t;
      if (person == null) {
        person = void 0;
      }
      if (person != null) {
        old = getPersons();
        matrix = getPriorityMatrix();
        if (old.length > persons.length) {
          $log.log('person deleted', person);
          delete matrix[person.id];
        } else {
          $log.log('person added', person);
          matrix[person.id] = {};
          ref = getTasks();
          for (j = 0, len = ref.length; j < len; j++) {
            t = ref[j];
            sum = 0;
            for (k = 0, len1 = old.length; k < len1; k++) {
              p = old[k];
              sum += matrix[p.id][t.id];
            }
            matrix[person.id][t.id] = sum === 0 ? sum : Math.floor(sum / old.length);
          }
        }
        setPriorityMatrix(matrix);
      }
      return $timeout(function() {
        return localStorage.setItem('workloadData-persons', JSON.stringify(persons, ['id', 'firstName', 'lastName']));
      }, 100);
    };
    getTasks = function() {
      return JSON.parse(localStorage.getItem('workloadData-tasks') || '[]');
    };
    setTasks = function(tasks, task) {
      var j, k, len, len1, matrix, old, p, per, ps;
      if (task == null) {
        task = void 0;
      }
      if (task != null) {
        old = getTasks();
        ps = getPersons();
        matrix = getPriorityMatrix();
        if (old.length > tasks.length) {
          $log.log('task deleted', task);
          for (j = 0, len = ps.length; j < len; j++) {
            p = ps[j];
            per = matrix[p.id];
            delete per[task.id];
          }
        } else {
          $log.log('task added', task);
          for (k = 0, len1 = ps.length; k < len1; k++) {
            p = ps[k];
            matrix[p.id][task.id] = 0;
          }
        }
        setPriorityMatrix(matrix);
      }
      return $timeout(function() {
        return localStorage.setItem('workloadData-tasks', JSON.stringify(tasks, ['id', 'taskName', 'activeDays']));
      }, 100);
    };
    _generatePriorityMatrix = function() {
      var j, k, len, len1, matrix, p, ps, t, ts;
      matrix = {};
      ps = getPersons();
      ts = getTasks();
      for (j = 0, len = ps.length; j < len; j++) {
        p = ps[j];
        matrix[p.id] = {};
        for (k = 0, len1 = ts.length; k < len1; k++) {
          t = ts[k];
          matrix[p.id][t.id] = 0;
        }
      }
      return matrix;
    };
    getPriorityMatrix = function() {
      var pm;
      pm = JSON.parse(localStorage.getItem('workloadData-priorityMatrix') || '[]');
      if (angular.isArray(pm)) {
        return _generatePriorityMatrix();
      } else {
        return pm;
      }
    };
    setPriorityMatrix = function(priorityMatrix) {
      return $timeout(function() {
        return localStorage.setItem('workloadData-priorityMatrix', JSON.stringify(priorityMatrix));
      }, 100);
    };
    getNextId = function(who) {
      var key, newId;
      key = 'workloadData-' + who + '-id';
      newId = parseInt(localStorage.getItem(key) || '0') + 1;
      localStorage.setItem(key, newId);
      return newId;
    };
    return service = {
      getPersons: getPersons,
      setPersons: setPersons,
      getTasks: getTasks,
      setTasks: setTasks,
      nextId: getNextId,
      getPriorityMatrix: getPriorityMatrix,
      setPriorityMatrix: setPriorityMatrix
    };
  });

}).call(this);



