(function() {
  angular.module('workloadPlanner').controller('MainController', function($timeout, webDevTec, toastr, $log) {
    'ngInject';
    var activate, getWebDevTec, showToastr, vm;
    vm = this;
    activate = function() {
      getWebDevTec();
      $timeout((function() {
        vm.classAnimation = 'rubberBand';
      }), 4000);
    };
    showToastr = function() {
      toastr.info('Fork <a href="https://github.com/Swiip/generator-gulp-angular" target="_blank"><b>generator-gulp-angular</b></a>');
      vm.classAnimation = '';
    };
    getWebDevTec = function() {
      vm.awesomeThings = webDevTec.getTec();
      angular.forEach(vm.awesomeThings, function(awesomeThing) {
        awesomeThing.rank = Math.random();
      });
    };
    vm.awesomeThings = [];
    vm.classAnimation = '';
    vm.creationDate = 1451903816692;
    vm.showToastr = showToastr;
    activate();
    vm.things = [
      {
        name: 'one'
      }, {
        name: 'two'
      }, {
        name: 'three'
      }, {
        name: 'four'
      }
    ];
    vm.remove = function($index, elem) {
      $log.log('elem', elem);
      elem._deleted = true;
      vm.things.splice($index, 1);
    };
  });

}).call(this);



